import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
} from '@umetrics/sartorius-ui-base';
import { dialogStyles } from './dialogStyle';
import { DialogProperties, DialogSlot } from './dialogProperties';
import { version } from './metadata';

import { DialogServiceEvents, DialogServiceOptions, DialogServiceSize } from '@umetrics/sartorius-ui-dialog-service';
import { DialogSize } from './dialogSize';
import { DialogEvent } from './dialogEvent';

@defineElement({
    tagName: 'dialog',
    styles: dialogStyles,
    version,
    //    sub: [Modal]
})
export class Dialog extends BaseElement implements DialogProperties {
    // Static members
    private static Modal;
    private static ModalSlot;

    // Private members

    // Protected members

    // Public members
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    hide = false;
    @property({ type: Boolean, reflect: true, attribute: 'no-overlay', converter: booleanConverter })
    noOverlay = false;
    @property({ type: Boolean, reflect: true, attribute: 'click-outside-close', converter: booleanConverter })
    clickOutsideClose = false;
    @property({ type: Boolean, reflect: true, attribute: 'escape-close', converter: booleanConverter })
    escapeClose = false;
    @property()
    header = '';
    @property({ type: DialogSize, attribute: 'modal-size' })
    modalSize = DialogSize.Default; /* Deprecated, use size */
    @property({ type: DialogServiceSize })
    size = DialogServiceSize.Default;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    resizeable = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    draggable = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter, attribute: 'fill-height' })
    fillHeight = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter, attribute: 'no-padding' })
    noPadding = false;
    @property({ type: Boolean, attribute: 'no-scroll', converter: booleanConverter })
    noScroll = false;
    @property({ type: Boolean, attribute: 'fixed-width', converter: booleanConverter })
    fixedWidth = false;

    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    get manager() {
        return this._manager;
    }
    set manager(value) {
        const prev = this._manager;
        if (value !== prev) {
            if (value) {
                this.hide = true;
                window.addEventListener(DialogServiceEvents.Show, (e) => this._managerShow(e));
                window.addEventListener(DialogServiceEvents.Hide, (e) => this._managerHide(e));
            } else {
                window.removeEventListener(DialogServiceEvents.Show, (e) => this._managerShow(e));
                window.removeEventListener(DialogServiceEvents.Hide, (e) => this._managerHide(e));
            }
            this._manager = value;
        }
    }

    private _manager = false;
    private _currentDialogServiceOptions?: DialogServiceOptions;
    private _unobserveChildChanges!: () => void;

    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-modal'), elem: ['Modal', 'ModalSlot'] });

        Dialog.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
        });
        this._managerHide = this._managerHide.bind(this);
        this._managerShow = this._managerShow.bind(this);
        this._unobserveChildChanges = this._observeChildSubtree();
    }

    disconnectedCallback() {
        super.disconnectedCallback();
        if (this._unobserveChildChanges) {
            this._unobserveChildChanges();
        }
        window.removeEventListener(DialogServiceEvents.Show, (e) => this._managerShow(e));
        window.removeEventListener(DialogServiceEvents.Hide, (e) => this._managerHide(e));
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            'fill-height': this.fillHeight,
            padding: !this.noPadding,
            'no-scroll': this.noScroll,
        });

        const header = this.header ? this.header : html`<slot name="${DialogSlot.HEADER_SLOT}"></slot>`;

        return html`
        <${Dialog.Modal.tag} class='${rootClasses} inner-modal' ?fill-height=${this.fillHeight} ?resizeable=${
            this.resizeable
        } ?draggable=${this.draggable} ?fixed-width=${this.fixedWidth} @modal-open=${(e) =>
            this._bubbleEvent(e)} @modal-close=${(e) => {
            this.hide = true;
            this._bubbleEvent(e);
        }} ?hide=${this.hide} ?no-overlay=${this.noOverlay} ?click-outside-close=${
            this.clickOutsideClose
        } ?escape-close=${this.escapeClose}
          modal-size="${this.size !== DialogServiceSize.Default ? this.size : this.modalSize}" ?no-scroll=${
              this.noScroll
          }>
            <h4 class='${baseClassName}-header' title=${this.header} slot='${Dialog.ModalSlot.TOP_SLOT}'>${header}</h4>
            <div class='${baseClassName}-content'>
                <slot></slot>
            </div>
            <div class='${baseClassName}-buttons' slot='${Dialog.ModalSlot.BOTTOM_RIGHT_SLOT}'>
                <slot name="${DialogSlot.BUTTON_SLOT}"></slot>
            </div>
        </${Dialog.Modal.tag}>
        `;
    }

    private _observeChildSubtree() {
        const observer = new MutationObserver(() => {
            if (this.shadowRoot) {
                const modal = this.shadowRoot.querySelector('[class$="inner-modal"]');
                if (modal) {
                    modal['_resetFocusableElements'](); // Ouch!
                }
            }
        });
        observer.observe(this, { subtree: true, childList: true, attributeFilter: ['disabled'] });
        return () => observer.disconnect();
    }

    private _bubbleEvent(e) {
        if (this.manager && this._currentDialogServiceOptions) {
            if (e.type === DialogEvent.Close) {
                this._copyChildren(this, this._currentDialogServiceOptions.root);
                if (this._currentDialogServiceOptions.hideCallback) {
                    this._currentDialogServiceOptions.hideCallback();
                }
                this._currentDialogServiceOptions = undefined;
            } else if (e.type === DialogEvent.Open) {
                if (this._currentDialogServiceOptions.showCallback) {
                    this._currentDialogServiceOptions.showCallback();
                }
            }
        }

        this.dispatchEvent(
            new CustomEvent(e.type, {
                ...e,
            })
        );
    }

    private _managerHide(e) {
        const service = e.detail.root;
        if (this._currentDialogServiceOptions && this._currentDialogServiceOptions.root === service) {
            this.hide = true;
            this._copyChildren(this, this._currentDialogServiceOptions.root);
            this._currentDialogServiceOptions = undefined;
        } else {
            console.warn('Unexpected hide from a service that didnt open dialog');
        }
    }

    private _managerShow(e) {
        if (this._currentDialogServiceOptions || !this.hide) {
            console.warn('Singleton error:Unexpected open, since dialog is already open');
        } else if (e.detail && e.detail.root) {
            this._currentDialogServiceOptions = e.detail;
            this._copyChildren(e.detail.root, this);
            this.hide = false;

            [
                //'noOverlay',
                //'clickOutsideClose',
                //'escapeClose',
                //'header',
                'size',
                'resizeable',
                'draggable',
            ].forEach((s) => {
                if (e.detail[s] !== undefined) {
                    this[s] = e.detail[s];
                }
            });
        }
    }

    private _copyChildren(src, dest) {
        while (src.children.length > 0) {
            dest.appendChild(src.children[0]);
        }
    }
}
