export enum DialogServiceSize {
    ExtraSmall = 'extra-small',
    Small = 'small',
    Default = 'default',
    Large = 'large',
}
export enum DialogServiceSlot {
    DEFAULT = '',
    HEADER_SLOT = 'header-slot',
    BUTTON_SLOT = 'button-slot',
}
export interface DialogServiceProperties {
    show?: boolean;

    size?: string | DialogServiceSize;
    resizeable?: boolean;
    draggable?: boolean;

    onOpen?: any;
    onClose?: any;
}
