import { css, cssSelector, cssVariableAsVar, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const dialogStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    const yellow = cssVariableAsVar('color_primary_1');
    const black = cssVariableAsVar('color_primary_3');

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                position: absolute;
            }

            ${root} {
                --modal-close-btn-padding-right: 8px;
            }

            ${child('todo')} {
                background: ${yellow};
                color: ${black};
            }

            ${child('header')} {
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                text-align: left;
                padding: 14px 16px 14px 24px;
                margin: 0;
                font-size: 16px;
            }

            ::slotted([name='header-slot']) {
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
            ${is('padding')} ${child('content')} {
                padding: 24px;
            }
            ${is('fill-height')} ${child('content')} {
                height: 100%;
            }

            ${child('buttons')} {
                display: flex;
                flex-direction: row;
                margin: 15px 24px;
                column-gap: 16px;
            }
        `,
    ];
};
